.footer{
    background-color: #24262b;
    padding: 20px 0 36px 36px;
	
}
.footer-col{
    width: 25%;
    padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left: 13px;
	bottom: -10px;
	background-color: #FF9F1C;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	color: #ffffff;
	transition: all 0.5s ease;
}

.footer-col .linkedin-icon {
	background-color: white;
	color: #24262b;
	border-radius: 50%;
}


/*responsive*/
@media(max-width: 767px){
    .footer-col{
    width: 50%;
    margin-bottom: 30px;
}
}
@media(max-width: 574px){
    .footer-col{
    width: 100%;
}
}


.product-card {
    width: 315px;
    position: relative;
    box-shadow: 0 2px 7px #dfdfdf;
    margin: 48px auto;
    background: #ffffff;
    border-radius: 10px;
}

.product-card:hover{ 
    box-shadow: -2px 2px 14px -2px #2E2E2E;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    transition: .5s ease-in-out;
}

.badge {
    position: absolute;
    left: 0;
    top: 20px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    background: red;
    color: #fff;
    padding: 3px 10px;
}

.product-tumb {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 230px;
    padding: 30px;
    background: white;
    border-radius: 12px;
}

.product-tumb img {
    max-width: 100%;
    max-height: 100%;
}

.product-details {
    padding: 20px 25px;
    height: 215px;
    background-color: #dcdcdc;
    border-bottom-left-radius:10px;
    border-bottom-right-radius:10px
}

.product-catagory {
    display: block;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    color: #838996;
    margin-bottom: 15px;
}

.product-details h4 {
    font-weight: 500;
    display: block;
    margin-bottom: 15px;
    text-transform: uppercase;
    color: #363636;
    text-decoration: none;
    transition: 0.3s;
    font-size: 15px;
}

.product-details h4:hover {
    color: black;
    transition: 0.5s;
    text-decoration: underline;
}

.product-details p {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 15px;
    color: #f4ca16;
}

.product-stock {
    font-size: 12px;
    line-height: 22px;
    margin-bottom: 15px;
    color: #838996;
    font-weight: 700;
}

.stock-0 {
    color: red;
}

.product-ratingStock {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.product-bottom-details {
    overflow: hidden;
    border-top: 1px solid #eee;
    padding-top: 20px;
}

.product-bottom-details div {
    float: left;
    width: 50%;
}

.product-price {
    font-size: 18px;
    color: #0070ba;
    font-weight: 600;
}

.product-price small {
    font-size: 80%;
    font-weight: 400;
    text-decoration: line-through;
    display: inline-block;
    margin-right: 5px;
}

.product-links {
    text-align: right;
    cursor: pointer;
}

.product-links .wishListTrue {
    color: #ffa500;
}
.product-links span {
    display: inline-block;
    margin-left: 6px;
    color: #808080;
    transition: 0.3s;
    font-size: 23px;
    
}

.product-links span:hover {
    color: #ffa500;
    font-size: 26px;
}

