    cursor: pointer;
  }

  .ddOpenclose 
  {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-color: #eeeeee;
    color: orange;
  }

  .ddMenuclose
  {
    display: block;
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
  }

  .ddHidden 
  {
    top: -1000px;
  }

  .ddVisible
  {
    top: 80px;
  }
  
  .smHidden 
  {
    max-height: 0px;
  }
}

