.dashboard-body{
    margin-left: 80px;
    margin-top: 80px;
    transition: margin-left 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.dashboard-header{
    width: 100%;
    height: 80px;
    background: #f5f5f5;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
}

.icon__menu{
    width: 50px;
    margin-top: 15px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
}

.dash-home-icon{
    color: white;
}

.icon__menu i{
    font-size: 20px;
    position: absolute;
}

.menu__side{
    width: 80px;
    height: 100%;
    background: #FF9F1C;
    position: fixed;
    top: 0;
    left: 0;
    color: white;
    font-size: 18px;
    z-index: 300;
    overflow: hidden;
    overflow-y: scroll;
    border-right: 20px solid #FF9F1C;
    transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

/*Ocultar scroll para chrome, safari y opera*/
.menu__side::-webkit-scrollbar{
    display: none;
}

/*Ocultar scroll para IE, Edge y Firefox*/
.menu__side{
    -ms-overflow-style: none;
    scrollbar-width: none;
}


.name__page{
    padding: 20px 24px;
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.name__page i{
    width: 20px;
    margin-right: 20px;
}

.options__menu{
    padding: 40px 30px;
    position: absolute;
    top: 80px;
}

.options__menu a{
    cursor: pointer;
    display: block;
    transition: color 300ms;
    color: white;
}

.options__menu a:hover{
    color: #24262b;
}

.options__menu .option{
    padding: 20px 0px;
    display: flex;
    align-items: center;
    position: relative;
}

.options__menu .option i{
    width: 20px;
    margin-right: 20px;
    cursor: pointer;
}

.options__menu .option h4{
    font-weight: 300;
    cursor: pointer;
}

.selected:before{
    content: '';
    width: 3px;
    height: 80%;
    background: white;
    position: absolute;
    top: 10%;
    left: -30px;
}

